<template>
  <div style="height:500px;width: 500px;">
    <div class="blue">1</div>
  </div>
</template>

<script>
export default {
  mounted() {
    this.start();
  },
  methods: {
    start() {
      this.$anime({
        targets: ".blue",
        backgroundColor: "#FFF",
        borderRadius: ["0%", "50%"],
        easing: "easeInOutQuad",
        loop: true
      });
    }
  }
};
</script>
<style scoped>
.blue {
  position: relative;
  right: 0;
  background-color: blue;
  width: 50px;
}
</style>